import {
    UPLOAD_POST,
    SET_MANY_POSTS,
    SET_POST,
    EDIT_POST,
    DELETE_POST,
} from '../types'
import axios from 'axios'

export const getPosts = () => (dispatch) => {
    return axios.get('/api/posts', {
        params: {
            lastDate: new Date(),
            limit: 10
        }
    })
        .then(res => {
            let userId = localStorage.userId
            dispatch({
                type: SET_MANY_POSTS,
                payload: res.data
            })
        })
}

export const getPost = (postId) => (dispatch) => {
    dispatch({
        type: SET_POST
    })
}

export const uploadPost = (post) => (dispatch) => {
    return axios.post('/api/posts', post)
        .then(res => {
            dispatch({
                type: UPLOAD_POST,
                payload: res.data.post
            })
            return res.data.message
        })
}

export const editPost = (postId, newPost) => (dispatch, getState) => {
    return axios
        .put(`/api/posts/${postId}`, newPost)
        .then(res => {
            let posts = getState().data.posts;
            dispatch({
                type: EDIT_POST,
                payload: posts.map((item) => {
                    if(item._id === postId) {
                        return res.data
                    }
                    return item
                })
            })
        })
} 
export const deletePost = (postId) => (dispatch, getState) => {
    return axios
        .delete(`/api/posts/${postId}`)
        .then(() => {
            let posts = getState().data.posts;
            dispatch({
                type: DELETE_POST,
                payload: posts.filter(post => post._id !== postId)
            })
        })
}