import React, { Component } from 'react'
import {
    TextField,
    Paper,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle, Avatar,
    Button,
    CircularProgress 
} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'

const styles = {
    dialog: {
        width: "400px",
        height: "300px"
    },
    input: {
        "width": "300px",
        "margin-top": "15px"
    },
}

class ProfileEditor extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            birthDate: "",
            company: "",
            introduction: "",
            location: ""
        }
    }
    handleKeyChange(e) {
        this.setState({[e.target.name]: e.target.value})
    }
    render() {
		const { classes, loading, open, closeDialog } = this.props;
		return (
			<Dialog open={open} onClose={closeDialog}>
                <div className = {classes.dialog}>
                    <Paper elevation = {4}>
                        <TextField 
                            onChange = {this.handleKeyChange} 
                            label = "Username" 
                            name = "username"
                            className = {classes.input}
                            disabled = {loading}
                        />
                        <br/>
                        <TextField 
                            onChange = {this.handleKeyChange} 
                            label = "Date of birth" 
                            name = "birthDate"
                            className = {classes.input}
                            disabled = {loading}
                        />
                        <br/>
                        <TextField 
                            onChange = {this.handleKeyChange} 
                            label = "Company" 
                            name = "company"
                            className = {classes.input}
                            disabled = {loading}
                        />
                        <br/>
                        <TextField 
                            onChange = {this.handleKeyChange} 
                            label = "Introduction" 
                            name = "introduction"
                            className = {classes.input}
                            disabled = {loading}
                        />
                        <br/>
                        <TextField 
                            onChange = {this.handleKeyChange} 
                            label = "Location" 
                            name = "location"
                            className = {classes.input}
                            disabled = {loading}
                        />
                        <br/>
                    </Paper>
                </div>
            </Dialog>
        )
    }
}

export default withStyles(styles)(ProfileEditor)
