import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime';
import {
    Card,
    CardActions,
    CardContent,
    CardHeader,
    Avatar,
    CircularProgress,
    IconButton,
} from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import CommentIcon from '@material-ui/icons/Comment'
import Share from '@material-ui/icons/Share'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Typography from '@material-ui/core/Typography';
import Options from '../Options' 
import PostEditor from './Editor' 
import Comment from './Comment'
import CommentCreator from './Comment/Creator'

import { connect } from 'react-redux'
import { likePost, unlikePost } from '../../redux/actions/likeActions'
import { editPost, deletePost } from '../../redux/actions/dataActions'
import { getComments } from '../../redux/actions/commentActions'

const styles = {
  card: {
    width: "600px",
    "position": "relative",
    "left": "50%",
    "top": "50%",
    "transform": "translate(-50%, 0%)",
    "margin-top": "25px",
    "margin-bottom": "25px"
  },
  avatar: {
      "background-color": "rgb(0, 188, 212)",
  },
  action: {
      display: "flex",
      "justify-content": "space-around",
  },
  liked: {
      "color": "rgb(0, 188, 212)"
  },
  number: {
    position: "relative",
	right: "6px"
  },
  comments: {
    "border-top": "1px solid #cccccc",
    "max-height": "500px",
    "overflow-y": "auto",
    "overflow-x": "hidden"
  },
  loading: {
    "position": "relative",
    "left": "50%",
    "top": "50%",
    "transform": "translate(-50%, 0%)"
  }
};

class Post extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            getCommentLoading: false,
            openPostEditor: false,
            editPostLoading: false,
            likeLoading: false,
        }
    }
    handleComments = (postId) => {
        this.setState({getCommentLoading: true})
        this.props.getComments(postId)
            .then(() => this.setState({getCommentLoading: false}))
            .catch(() => this.setState({getCommentLoading: false}))
    }

    openPostEditor = () => {
        this.setState({openPostEditor: true})
    }
    closePostEditor = () => {
        this.setState({openPostEditor: false})
    }
    handleEdit = (postContent) => {
        this.setState({editPostLoading: true})
        const newPost = {
            content: postContent
        }
        this.props.editPost(this.props.post._id, newPost)
            .then(() => {
                this.setState({editPostLoading: false})
                this.closePostEditor()
            })
            .catch(() => {
                this.setState({editPostLoading: false})
                this.closePostEditor()
            }) 
    }
    render() {
        const { classes, post, likePost, unlikePost } = this.props;
        const { likedByUser, comments, commentCount } = post
        const postOptions = [
            {
                text: "Edit post",
                action: this.openPostEditor
            },
            {
                text: "Delete post",
                action: () => this.props.deletePost(post._id)
            },
            {
                text: "Disable share",
                action: () => {}
            },
            {
                text: "Disable comment",
                action: () => {}
            }
        ]
        dayjs.extend(relativeTime);
        return(
            <Card elevation = {4} key = {post._id} className={classes.card}>
                <CardHeader
                    avatar={
                        <Avatar className={classes.avatar}>
                            {post.author[0].toUpperCase()}
                        </Avatar>
                    }
                    title = {post.author}
                    subheader= {dayjs(post.createdAt).fromNow()}
					action = {
						(post.authorId === JSON.parse(localStorage.user)._id)
						? (
							<Options optionsList = {postOptions} displayComponent = {<MoreVertIcon/>}/>
						) : (
							""
						)
					}
                />
                <CardContent>
                <Typography variant="body2" component="p">
                    {post.content}
                </Typography>
                </CardContent>
                <CardActions className = {classes.action}>
                    <div>
                        <IconButton
                            title = "Like" 
                            onClick = {
                                (likedByUser) 
                                ? () => {
                                    unlikePost(post._id, () => this.setState({likeLoading: true}))
                                        .then((res) => {
                                            this.setState({likeLoading: false})
                                        })
                                        .catch((err) => {
                                            this.setState({likeLoading: false})
                                        })
                                }
                                : () => {
                                    likePost(post._id, () => this.setState({likeLoading: true}))
                                        .then((res) => {
                                            this.setState({likeLoading: false})
                                        })
                                        .catch((err) => {
                                            this.setState({likeLoading: false})
                                        })
                                }
                            }
                            disabled = {this.state.likeLoading}
                        >
                            <FavoriteIcon className = {(likedByUser) ? classes.liked : ""}/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {post.likeCount}
                        </Typography>
                    </div>
                    <div>
                        <IconButton title = "Comment" onClick = {
                            () => this.handleComments(post._id)
                        }>
                            <CommentIcon/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {commentCount}
                        </Typography>
                    </div>
                    <IconButton title = "Share">
                        <Share/>
                    </IconButton>
                </CardActions>
                    {comments ? (
                        <div>
                            <div className = {classes.comments}>
                                {comments.map((comment) => 
                                        <Comment key = {comment._id} comment = {comment} postId = {post._id}/>
                                )}
                            </div>
                            <CommentCreator author = {post.author} postId = {post._id}/>
                        </div>
                        ) : (
                            this.state.getCommentLoading ? (
                                    <CircularProgress size = {34} className = {classes.loading}/>
                                ) : (
                                    ""
                                )
                        )}
            <PostEditor 
              loading = {this.state.editPostLoading} 
              open = {this.state.openPostEditor}
              closeDialog = {this.closePostEditor}
              handleUpdate = {this.handleEdit}
              mode = "Update"
              defaultValue = {post.content}
            />
            </Card>
        )
    }
}

const mapActionsToProps = {
    likePost,
    unlikePost,
	getComments,
    editPost,
    deletePost
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(Post))