import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Avatar } from '@material-ui/core'
import Options from '../components/Options' 

import { connect } from 'react-redux'
import { toggleSideNav } from '../redux/actions/uiActions'
import { logout } from '../redux/actions/userActions'

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    "margin-bottom": "25px"
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  avatar: {
    "background-color": "rgb(0, 188, 212)",
  },
  toolBar: {
    maxHeight: 62,
    minHeight: 60
  }
});

class Header extends React.Component {
  render(){
    const { classes, toggleSideNav, siteTitle} = this.props;
    const userOptions = [
      {
        text: "Log out",
        action: this.props.logout
      },
    ]
    return (
        <div className={classes.root}>
          <AppBar position="static">
            <Toolbar className = {classes.toolBar}>
              <IconButton edge="start" className={classes.menuButton} color="inherit" onClick = {toggleSideNav}>
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                {siteTitle}
              </Typography>
              <Options optionsList = {userOptions} displayComponent = {
                <Avatar className = {classes.avatar}>
                  {JSON.parse(localStorage.user).username[0].toUpperCase()}
                </Avatar>
              }/>
            </Toolbar>
          </AppBar>
        </div>
      );
  }
}

const mapActionsToProps = {
    logout,
    toggleSideNav
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(Header))
